import { TabPane } from "reactstrap";

const Applicant = ({ applicant }) => {
	return (
		<TabPane tabId="2">
			<div className="mb-1">
				<h1 className="title mb-1">Información Personal</h1>
				<div className="info-tab-wrappper">
					<div>
						<div className="group-wrapper">
							<h5 className="subtitle">Apellido Paterno</h5>
							<p>{applicant?.paternal_surname ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<p className="subtitle">RFC:</p>
							<p>{applicant?.rfc ?? "N/A"}</p>
						</div>
					</div>
					<div>
						<div className="group-wrapper">
							<h5 className="subtitle">Apellido Materno</h5>
							<p>{applicant?.maternal_surname ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<p className="subtitle">NSS:</p>
							<p>{applicant?.nss ?? "N/A"}</p>
						</div>
					</div>
					<div>
						<div className="group-wrapper">
							<h5 className="subtitle">Nombre(s)</h5>
							<p>{applicant?.name ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<p className="subtitle">CURP:</p>
							<p>{applicant?.curp ?? "N/A"}</p>
						</div>
					</div>
				</div>
			</div>
			<div>
				<h1 className="title mb-1">Dirección</h1>
				<div className="info-tab-wrappper">
					<div className="group-wrapper">
						<h5 className="subtitle">C.P.</h5>
						<p>{applicant?.address?.zip_code ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">COLONIA</h5>
						<p>{applicant?.address?.colony ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Delegación o Municipio</h5>
						<p>{applicant?.address?.municipality ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Calle</h5>
						<p>{applicant?.address?.street ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Num. Exteriror</h5>
						<p>{applicant?.address?.outer_number ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Num. Interior</h5>
						<p>{applicant?.address?.inner_number ?? "N/A"}</p>
					</div>
				</div>
			</div>
		</TabPane>
	);
};

export default Applicant;
