import { useState }              from "react";
import { connect }               from "react-redux";
import { useParams, useHistory } from "react-router-dom";

//Our Imports
import { bindAll }                        from "Helpers";
import { ModalSlice }                     from "Store/Slices";
import { useCan, useChangeRequestStatus } from "Helpers/Hooks";
import { genericApi  }                    from "Store/api/genericApi";
import RequestDeteails                    from "./RequestDeteailsPage";

const RequestDeteailsContainer = ({
	modalSliceActions,
}) => {

	const { id }   = useParams();
	const history  = useHistory();

	const [ can ] = useCan();

	const {
		data : requestInfo,
		isLoading : isLoadingData,
	} = genericApi.useGetDataQuery({ module : `petitions/${id}`, tags : ["petition_details"] });

	const [ handleChangeStatus, { isLoading } ] = useChangeRequestStatus({ request : requestInfo?.data ?? {} });

	const [dropdownOpen, setDropdownOpen] = useState({});

	const toggleDropdown = (key) => () => {
		setDropdownOpen(prev => ({ [key] : !prev[key] }));
	};

	const redirectToEdit = (id, type) => () => history.push(`/app/create_request/${id}?type=${type}`);

	const handleDeleteBtn = (request) => () => {
		modalSliceActions.openModal({
			type  : "confirmationModal",
			props : {
				_id                  : request._id,
				identifier           : request.key,
				name                 : "Solicitud",
				title                : "Eliminar solicitud",
				confirmationQuestion : "¿Estás seguro de eliminar la solicitud?",
				bodyText             : "No podrás recuperar la solicitud después de eliminarla.",
				confirmLabel         : "Eliminar",
				actionToDispatch     : "deleteRequest",
				replacePage          : true,
			},
		});
	};

	if (isLoadingData) {
		return <>Cargando..</>;
	}


	return (
		<RequestDeteails
			delegations={{
				can,
				isLoading,
				dropdownOpen,
				redirectToEdit,
				toggleDropdown,
				handleDeleteBtn,
				handleChangeStatus,
				request : requestInfo?.data ?? {},
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(null, mapDispatchToProps)(RequestDeteailsContainer);
