import { TabPane } from "reactstrap";

import {
	roomOptions,
	levelOptions,
	typologyOptions,
	roadTypeOptions,
	zoneTypeOptions,
	settlementTypeOptions,
} from "Resources/data/RequestForm";

const livingPlaceOptions = [
	"Nivel",
	"Entrada",
	"Edificio",
	"Condominio",
	"Departamento",
];

const housing_types = [ "Vivienda Nueva", "Vivienda Usada" ];

const LivingPlaceTab = ({ request }) => {
	return (
		<TabPane tabId="1">
			<div className="mb-1">
				<h1 className="title mb-1">{housing_types?.[request?.housing_type]?.label ?? "Vivienda"}</h1>
				<div className="info-tab-wrappper">
					<div className="group-wrapper">
						<h5 className="subtitle">C.P.</h5>
						<p>{request?.housing?.zip_code ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Entidad Federativa</h5>
						<p>{request?.housing?.id_place?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Delegación o Municipio</h5>
						<p>{request?.housing?.municipality ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Localidad</h5>
						<p>{request?.housing?.town ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Asentamiento</h5>
						<p>{request?.housing?.settlement ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Asentamiento</h5>
						<p>{settlementTypeOptions?.[request.housing?.settlement_type]?.label ?? "N/A"}</p>
					</div>
				</div>
			</div>

			<div className="mb-1">
				<div className="info-tab-wrappper">
					<div className="group-wrapper">
						<h5 className="subtitle">Vialidad/Calle</h5>
						<p>{request?.housing?.street_name ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Vialidad</h5>
						<p>{roadTypeOptions?.[request.housing?.street_type]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Zona</h5>
						<p>{zoneTypeOptions?.[request.housing?.zone_type]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Supermanzana</h5>
						<p>{request?.housing?.sub_section ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Manzana</h5>
						<p>{request?.housing?.section ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Lote</h5>
						<p>{request?.housing?.lot ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">No. Exteriror</h5>
						<p>{request?.housing?.outer_number ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">No. Interior</h5>
						<p>{request?.housing?.inner_number ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Vivienda</h5>
						<p>{livingPlaceOptions?.[request?.housing?.house_type]?.label ?? "N/A"}</p>
					</div>
				</div>
			</div>

			<div>
				<h1 className="title mb-1">Vialidad</h1>
				<div className="info-tab-wrappper">
					<div className="group-wrapper">
						<h5 className="subtitle">Primera Entrevialidad</h5>
						<p>{request?.housing?.street_between_1_name ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Vialidad</h5>
						<p>{roadTypeOptions?.[request.housing?.street_between_1_type]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Segunda Entrevialidad</h5>
						<p>{request?.housing?.street_between_2_name}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Vialidad</h5>
						<p>{roadTypeOptions?.[request.housing?.street_between_2_type]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Vialidad Posterior</h5>
						<p>{request?.housing?.street_back_name ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipo de Vialidad</h5>
						<p>{roadTypeOptions?.[request.housing?.street_back_type]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Referencia</h5>
						<p>{request?.housing?.reference ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Latitud</h5>
						<p>{request?.housing?.lat ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Longitud</h5>
						<p>{request?.housing?.lng  ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Tipologia</h5>
						<p>{typologyOptions?.[request.housing?.typology]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Nivel</h5>
						<p>{levelOptions?.[request.housing?.level]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Area de Construcción</h5>
						<p>{request.housing?.construction_area ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Area de Terreno</h5>
						<p>{request.housing?.land_area ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Número de Recamaras</h5>
						<p>{roomOptions?.[request.housing?.number_rooms]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Número de Baños</h5>
						<p>{roomOptions?.[request.housing?.number_bathrooms]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Estacionamiento</h5>
						<p>{roomOptions?.[request.housing?.parking_lot]?.label ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Estacionamiento</h5>
						<p>{request.housing?.approximate_value ?? "N/A"}</p>
					</div>
				</div>
			</div>
			{
				request?.housing_type === 1 &&
				<div>
					<h1 className="title mb-1">Información Notaria</h1>
					<div className="info-tab-wrappper">
						<div className="group-wrapper">
							<h5 className="subtitle">Número del Notario</h5>
							<p>{request?.housing?.notary_number ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<h5 className="subtitle">Nombre del Notario</h5>
							<p>{request?.housing?.notary_name ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<h5 className="subtitle">Nombre del Notario</h5>
							<p>{request?.housing?.place ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<h5 className="subtitle">No. Escritura</h5>
							<p>{request?.housing?.deed_number ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<h5 className="subtitle">Libro</h5>
							<p>{request?.housing?.book ?? "N/A"}</p>
						</div>
						<div className="group-wrapper">
							<h5 className="subtitle">Volumen</h5>
							<p>{request?.housing?.volume_book}</p>
						</div>
					</div>
				</div>
			}
		</TabPane>
	);
};

export default LivingPlaceTab;
