import {
	Card,
	CardBody,
	CardTitle,
	CardHeader,
} from "reactstrap";
import { Inbox } from "react-feather";

//Our Importations
import { isValidArray } from "Helpers";
import Timeline         from "Components/Global/Timeline";
import "./RequestHistory.scss";

const RequestHistory = ({
	delegations : {
		page,
		totalDocs,
		historyData,
		fetchMoreData,
	},
}) => {
	return (
		<Card id="RequestHistory">
			<CardHeader>
				<CardTitle tag='h4'>
					Historial
				</CardTitle>
			</CardHeader>
			<CardBody>
				{
					!isValidArray(historyData) ? (
						<div className="empty-history">
							<Inbox />
							<h5>No hay historial disponible</h5>
						</div>
					) : (
						<Timeline
							page={page}
							data={historyData}
							totalDocs={totalDocs}
							fetchMoreData={fetchMoreData}
						/>
					)
				}
			</CardBody>
		</Card>
	);
};

export default RequestHistory;
