import {
	Card,
	Nav,
	NavItem,
	NavLink,
	TabContent,
	CardBody,
} from "reactstrap";

//Our Imports
import { ApplicantTab, OwnerTab } from "../NavTabs";
import LivingPlaceTab             from "../NavTabs/LivingPlaceTab";
import "./RequestNavDetails.scss";


const RequestNavDetails = ({
	delegations : {
		request,
		activeTab,
		toggleTab,
	},
}) => {
	return (
		<div id="RequestNavDetails">
			<Card>
				<Nav className="nav-styles" tabs fill>
					<NavItem>
						<NavLink
							active={activeTab === "1"}
							onClick={toggleTab("1")}
						>
							{ request?.petition_type < 1 ? "Vivienda" : "Linea 3" }
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={activeTab === "2"}
							onClick={toggleTab("2")}
						>
							Solicitante
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={activeTab === "3"}
							onClick={toggleTab("3")}
						>
							Propietario
						</NavLink>
					</NavItem>

				</Nav>
				<CardBody>
					<TabContent className="py-50" activeTab={activeTab}>
						<LivingPlaceTab request={request} />
						<ApplicantTab applicant={request?.applicant} />
						<OwnerTab owner={request?.owner} />
					</TabContent>
				</CardBody>
			</Card>
		</div>
	);
};

export default RequestNavDetails;
