import { TabPane } from "reactstrap";

const OwnerTab = ({ owner }) => {
	return (
		<TabPane tabId="3">
			<div className="mb-1">
				<h1 className="title mb-1">Información Personal</h1>
				<div className="info-tab-wrappper">
					<div className="group-wrapper">
						<h5 className="subtitle">Nombre(s)</h5>
						<p>{owner?.name ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<p className="subtitle">CURP:</p>
						<p>{owner?.curp ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<p className="subtitle">RFC:</p>
						<p>{owner?.rfc ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<p className="subtitle">Correo:</p>
						<p>{owner?.email ?? "N/A"}</p>
					</div>
				</div>
			</div>
			<div>
				<h1 className="title mb-1">Dirección</h1>
				<div className="info-tab-wrappper">
					<div className="group-wrapper">
						<h5 className="subtitle">C.P.</h5>
						<p>{owner?.address?.zip_code ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">COLONIA</h5>
						<p>{owner?.address?.colony ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Delegación o Municipio</h5>
						<p>{owner?.address?.municipality ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">Calle</h5>
						<p>{owner?.address?.street ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">No. Exterior</h5>
						<p>{owner?.address?.outer_number ?? "N/A"}</p>
					</div>
					<div className="group-wrapper">
						<h5 className="subtitle">No. Interiro</h5>
						<p>{owner?.address?.inner_number ?? "N/A"}</p>
					</div>
				</div>
			</div>
		</TabPane>
	);
};

export default OwnerTab;
