import { useState } from "react";

//Our Imports
import RequestNavDetails from "./RequestNavDetails";

const RequestNavDetailsContainer = ({ request }) => {
	const [activeTab, setActiveTab] = useState("1");

	const toggleTab = (tab) => () => setActiveTab(tab);

	return (
		<RequestNavDetails
			delegations={{
				request,
				activeTab,
				toggleTab,
			}}
		/>
	);
};

export default RequestNavDetailsContainer;
