import classNames      from "classnames";
import { Edit, Trash } from "react-feather";
import {
	Card,
	CardBody,
	DropdownItem,
	DropdownMenu,
	DropdownToggle,
	ButtonDropdown,
} from "reactstrap";

//Our Imports
import UserImage         from "Components/Global/UserImage";
import ListOfFiles       from "Components/Global/ListOfFiles";
import BtnsActions       from "Components/Global/BtnsActions";
import RequestHistory    from "Components/Request/RequestHistory";
import RequestNavDetails from "Components/Request/RequestNavDetails";
import "./RequestDeteailsPage.scss";


const statusTypes = [
	{ label : "Captura",     value : 0 },
	{ label : "Seguimiento", value : 1 },
	{ label : "Registrada",  value : 2 },
];

const reqtypes = [
	[
		"Vivienda Nueva",
		"Vivienda Usada",
	],
	"Línea 3",
];

const RequestDeteailsPage = ({
	delegations : {
		can,
		request,
		dropdownOpen,
		toggleDropdown,
		redirectToEdit,
		handleDeleteBtn,
		handleChangeStatus,
	},
}) => {
	return (
		<div id="RequestDeteailsPage">
			<div className="header-page mb-3">
				<div className="title-wrapper">
					<h1 className="title">Solicitud</h1>
					<h1 className="-">-</h1>
					<h1 className="title">#{request?.key}</h1>
				</div>
				{
					<ButtonDropdown
						disabled={can("update", "request", "!status")}
						isOpen={dropdownOpen[request?._id]}
						toggle={toggleDropdown(request?._id)}
					>
						<DropdownToggle
							caret
							outline
							color="primary"
							className={can("update", "request", "!status") ? "disabled" : "" }
						>
							{statusTypes[request?.status ?? 0].label}
						</DropdownToggle>
						<DropdownMenu>
							{
								statusTypes.map(statusType => (
									statusType.value !== request?.status &&
									<DropdownItem
										key={statusType.value}
										tag="a"
										onClick={handleChangeStatus(request, statusType.value)}
									>
										{statusType.label}
									</DropdownItem>
								))
							}
						</DropdownMenu>
					</ButtonDropdown>
				}
			</div>

			<div className="wrapper-request-details">
				<div className="wrapper-info-details">
					<div className="mb-1">
						<RequestHistory request={request} />
					</div>
					<RequestNavDetails request={request} />

					<ListOfFiles
						files={request?.files ?? []}
					/>
				</div>
				<div>
					<BtnsActions
						primaryBtnLabel="Editar"
						primaryBtnIcon={<Edit size={16} />}
						handlePrimaryBtn={redirectToEdit(request?._id, request?.petition_type)}
						showSecundaryBtn={false}
						tertiaryBtnLabel="Eliminar"
						tertiaryBtnIcon={<Trash size={16} />}
						handletertiaryBtn={handleDeleteBtn(request)}
					/>
					<Card className="mt-1">
						<CardBody>
							<div className="mb-2">
								<h1 className="title-type mb-1">Tipo de Solicitud</h1>
								<p
									className={classNames("request-type", {
										"request-type-color-1" : request?.housing_type === 0,
										"request-type-color-2" : request?.petition_type === 1,
									})}
								>
									{
										request?.petition_type > 0 ?
											reqtypes[request?.petition_type]
											:
											reqtypes[0][request?.housing_type]
									}
								</p>
							</div>
							<div className="mb-2">
								<h1 className="title-type mb-1">Valuador</h1>
								<div className="user-name-wrapper">
									<UserImage
										rounded
										size={28}
										image=""
										name={request?.valuer?.name}
									/>
									<p className="name">{request?.valuer?.name}</p>
								</div>
							</div>
							<div>
								<h1 className="title-type mb-1">Controlador</h1>
								<div className="user-name-wrapper">
									<UserImage
										rounded
										size={28}
										image=""
										name={request?.controller?.name}
									/>
									<p className="name">{request?.controller?.name}</p>
								</div>
							</div>
						</CardBody>
					</Card>
					<Card className="mt-1">
						<CardBody>
							<h1 className="title-type">RUV</h1>
							<p className="name mb-1">{request?.ruv || "--"}</p>
							<h1 className="title-type">CUV</h1>
							<p className="name">{request?.cuv || "--"}</p>
						</CardBody>
					</Card>

				</div>
			</div>
		</div>
	);
};

export default RequestDeteailsPage;
