import { useEffect, useRef, useState } from "react";
import { ChevronRight }                from "react-feather";

//Our Importations
import { isValidArray, dayjs } from "Helpers";
import { genericApi }          from "Store/api/genericApi";
import RequestHistory          from "./RequestHistory";

const status  = ["Capturada", "Seguimiento", "Registrada"];

const RequestHistoryContainer = ({ request }) => {

	const page       = useRef(1);
	const totalDocs  = useRef(1);
	const prevStatus = useRef(null);

	const [historyData, sethistoryData] = useState([]);

	const [ fetchHistory ] = genericApi.useLazyGetDataQuery();

	const getColor = () => {
		const colors = ["info", "primary", "success", "warning"];
		const random = Math.floor(Math.random() * colors.length);
		return colors[random];
	};

	const fetchMoreData = async () => {
		if (!request) return;

		const prevHistoryData = (prevStatus.current !== request.status) ? [] : historyData;

		if (prevStatus.current !== request.status && prevStatus.current !== null) {
			page.current = 1;
		}

		const { docs, totalPages } = await fetchHistory({
			params : {
				limit : 5,
				page  : page.current,
			},
			tags   : ["history"],
			module : `petitions/${request?._id}/history`,
		}).unwrap();

		totalDocs.current = totalPages;

		if (totalPages > 1) {
			page.current = page.current + 1;
		}

		prevStatus.current = request.status;
		buildHistoryArray(docs, prevHistoryData);
	};

	const buildHistoryArray = (docs, prevHistoryData) => {
		if (!isValidArray(docs)) return;

		const data = [...prevHistoryData];

		for (const item of docs) {
			data.push({
				title         : `La solicitud #${item.petition.key} ha cambiado de status:`,
				color         : item?.subject ? "danger" : getColor(),
				meta          : dayjs(item.createdAt).format("DD/MM/YYYY h:mm A"),
				customContent : (
					<>
						<div className="custom-component-style">
							<p className="color-1">{status[item.prev_status]}</p>
							<ChevronRight size={12} />
							<p className="color-2">{status[item.new_status]}</p>
						</div>
						<p  className="text-danger">{item?.subject}</p>
					</>
				),
			});
		}

		sethistoryData(data);
	};

	useEffect(() => fetchMoreData(), [request]);

	return (
		<RequestHistory
			delegations={{
				page      : page.current,
				totalDocs : totalDocs.current,
				historyData,
				fetchMoreData,
			}}
		/>
	);
};

export default RequestHistoryContainer;
